@import url("https://rsms.me/inter/inter-ui.css");
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

@media (prefers-reduced-motion: reduce) {
  * {
    -webkit-animation: none !important;
            animation: none !important;
    transition: none !important; } }

html {
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  text-rendering: optimizeLegibility; }

html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden; }

input,
textarea,
button,
select,
a,
div {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  font-family: 'Inter UI', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif; }

::-webkit-scrollbar-thumb {
  background: white;
  border-radius: 10px; }

@supports (font-variation-settings: normal) {
  body {
    font-family: 'Inter UI var alt', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif; } }

.MainPage {
  flex: 1 1;
  display: flex;
  overflow: hidden;
  padding: 0 1.5rem; }

.mainTable {
  flex: 1 1;
  height: 100%;
  overflow-y: scroll; }

.sideBar {
  flex: 0 1 20rem;
  padding: 0 1.5rem;
  height: 100%;
  overflow-y: scroll; }

::-webkit-scrollbar {
  display: block;
  width: 0.5rem;
  position: absolute; }

::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 0.5rem; }

:hover::-webkit-scrollbar-track {
  background: rgba(231, 238, 243, 0.5); }

:hover::-webkit-scrollbar-thumb {
  background: #e61c1c; }
